@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600&display=swap);
/*Import Poppins from Google Fonts*/

body {
  margin: 0;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*App CSS*/
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: Poppins;
  min-height: 100vh;
}

.App-logo {
  height: 75px;
}

.App-title{
  font-size: 2.5rem;
  margin-bottom: 1rem;
  font-weight: bold;
}
.App-text{
  font-size: 1.25rem;
  font-weight: 500;
  margin-bottom: 2.5rem;
}

.App-secondary-button{
  border-radius: 100rem;
  margin: 0.5rem 0rem;
  border: 2px solid white;
  background-color: transparent;
  font-size: 1.25rem;
  padding: 1rem 2rem;
  font-weight: bold;
  color: white;
  font-family: Poppins;
  margin-right: 15px; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: fadeout 0.5s;
}

.App-secondary-button:hover{

  border-radius: 100rem;
    margin: 0.5rem 0rem;
    border: 2px solid  white;
    background-color: white;
    font-size: 1.25rem;
    padding: 1rem 2rem;
    font-weight: bold;
    color: #C71C4F;
    font-family: Poppins;
    cursor:pointer;
    margin-right: 15px; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
    animation: fadein 0.5s;

}

.App-know-more-button{
  border-radius: 100rem;
  margin: 0.5rem 0rem;
  border:0px;
  background-color: white;
  font-size: 1rem;
  padding: 1rem 2rem;
  font-weight: 500;
  color: #C71C4F;
  font-family: Poppins;
  margin-right: 15px;
}
.App-
.App-link {
  color: #61dafb;
}

.App-link-details{
  text-decoration: underline;
  color:white;
  font-weight: 600;
  text-decoration: none;
}

.App-link-details:hover{
  text-decoration: underline;
  color:white;
  font-weight: 600;
}

.App-details{
  background-color: white;
  min-width: 75%;
  padding: 10px;
}





#Animations
.fade-enter{
  opacity:0.01;
}
.fade-enter{
  opacity:0.01;
}
.fade-enter.fade-enter-active{
  opacity:1;
  transition: opacity 350ms ease-in;
}
.fade-exit{
  opacity:1;
}
.fade-exit.fade-exit-active{
  opacity:0.01;
  transition: opacity 350ms ease-in;
}


@keyframes fadein {
  from { border-radius: 100rem;
    margin: 0.5rem 0rem;
    border: 2px solid white;
    background-color: transparent;
    font-size: 1.25rem;
    padding: 1rem 2rem;
    font-weight: bold;
    color: white;
    font-family: Poppins;
    margin-right: 15px; }
  to   { border-radius: 100rem;
    margin: 0.5rem 0rem;
    border: 2px solid  white;
    background-color: white;
    font-size: 1.25rem;
    padding: 1rem 2rem;
    font-weight: bold;
    color: #C71C4F;
    font-family: Poppins;
    cursor:pointer;
    margin-right: 15px; }
}

