.Card-body{
    background-color: white;
    border-radius: 10px;
    height: 100%;
    max-height: 500px;
    box-shadow: 0px 15px 20px rgba(150, 50, 85, 0.1);
    transition: transform .3s ease-out;
}

.Card-title{
    color: #BE5A78;
    font-weight: 800;
    font-size: 1.5rem;
}

.Card-img{
    width: 100%;
    border-radius: 10px;
}

  .Card-body:hover {
    transform: translate(0, -5px);
    cursor: pointer;
    box-shadow: 0px 15px 20px rgba(150, 50, 85, 0.05);
  }
.Tradition-overlay{
    background-color: rgba(255, 255, 255, 0.85);
    width: 100%;
    min-height: 100vh;
    padding: 2rem 1rem 3rem 1rem;
}

.Tradition-icon{
    height: 50px;
    margin-top: 3rem;
    margin-bottom: 2rem;
}

.Tradition-quote{
    font-weight: bold;
    font-size: 2rem;
    line-height: 3.5rem;
    margin-bottom: 2rem;
    color: #B44367;
}

.Tradition-source{
    font-weight: 600;
    color: #9e2d52;
    font-size: 1rem;
    margin: 2rem 0rem 3rem 0rem;
}

.Tradition-button{
    border-radius: 100rem;
    margin: 1rem 1rem;
    border: 3px solid #1e1cb9;
    background-color: #1e1cb9;
    font-size: 1.2rem;
    padding: 1rem 2rem;
    font-weight: 600;
    color: white;
}

.Tradition-repeat-button{
    border-radius: 100rem;
    margin: 1rem 1rem;
    border: 3px solid #9e2d52;
    background-color: transparent;
    font-size: 1.2rem;
    padding: 1rem 2rem;
    font-weight: 600;
    color: #9e2d52;
}
